.navbar{
    margin: 32px 0;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #1E212B;
}

.navbar__link{
    display: block;
    color: #1E212B;
    background-color: #00d6ff;
    text-decoration: none;
    line-height: 30px;
    text-align: center;
    padding-top: 5px;
    width: 100%;
    height: 30px;   
    
}

button{
    color: #1E212B;
    background-color: #00d6ff;    
    border-radius: 5px;
    margin: 0 30px;
    padding-top: 1px;
    height: 40px;
    width: 100px;
    font-weight: bold;

}

@media only screen and (max-width: 768px){
    .navbar__link{
        height: 50px;
        width: 120px;
        padding-top: 15px;
    }

    button{
        height: 70px;
        width: 140px;
    }

}