.content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 100px;
    font-weight: bold;
}

.content__text{
    padding-top: 60px;
    padding-left: 360px;
    padding-right: 10px;
    line-height: 30px;

}

.content__logo{
    height: 250px;
    position: absolute;
    top: 170px;
    left: 70px;
    
}

.content__images{
    margin-top: 30px;
    margin-bottom: 140px; 
}

.content__name{
    display: flex;
    position: absolute;
    margin-bottom: 180px;
    margin-left: 10px;
}

.content__icons{
    display: none;
}

.color{
    color: #a800ff ;
}

@media only screen and (max-width: 768px){
    .content__logo{
        position: relative;
        
    }

    .content__name{
        position: relative;
        left: 150px
    }

    .content__text{
        padding-top: 180px;
        padding-bottom: 20px;
        padding-right: 260px;
        
    }

}